<template>

    <div class="entry" @touchmove="touchmove" @touchend="touchend"  >
      <navbar :id="currentPage" @linkTo="linkTo"></navbar>
      <home
      id="0"
      :class="{
        currentPage: currentPage === 0,
        prePage: currentPage > 0,
        nextPage: currentPage < 0
      }" ></home>
      <project
            id="1"
      :class="{
        currentPage: currentPage === 1,
        prePage: currentPage > 1,
        nextPage: currentPage < 1
      }"
      ></project>
      <blog       id="2"
      :class="{
        currentPage: currentPage === 2,
        prePage: currentPage > 2,
        nextPage: currentPage < 2
      }"
      ></blog>
      <skill
            id="0"
      :class="{
        currentPage: currentPage === 3,
        prePage: currentPage > 3,
        nextPage: currentPage < 3
      }"
      ></skill>
      <about
            id="0"
      :class="{
        currentPage: currentPage === 4,
        prePage: currentPage > 4,
        nextPage: currentPage < 4
      }"
      ></about>
    </div>

</template>

<script>
import home from '@/page/1home/template.vue'
import project from '@/page/2project/template.vue'
import blog from '@/page/3blog/template.vue'
import skill from '@/page/4skill/template.vue'
import about from '@/page/5aboutMe/template.vue'
import navbar from '@/components/nav.vue'
export default {
  name: 'App',
  components:{
    home,project,blog,skill,about,navbar
  },
  data:()=>({
    currentPage: 0,
  }),
  mounted () {
    // scrolling listener
       window.onmousewheel = e => {
      e.stopPropagation()
      e.preventDefault()
        if(!this.lock===true) 
     if(e.wheelDelta<0){
      
          this.goDown()
      }
      if(e.wheelDelta>0){
          this.goUp()
      }
      
    }
  },
  methods:{
        linkTo (id) {
      this.currentPage = id
    },
     goDown(){
      if(this.currentPage>=0 && this.currentPage<=3){
          console.log(22);
        this.currentPage=this.currentPage+1
        this.lock = true;
        setTimeout(()=>{
          this.lock = false;
        },800)
      }
    },
    goUp(){
      if(this.currentPage>=1&&this.currentPage<=5){

        this.currentPage=this.currentPage-1
                this.lock = true;
        setTimeout(()=>{
          this.lock = false;
        },800)
      }
    },
    touchmove (e) {
      e.preventDefault()
      if (this.touchStartX !== 0) return
      this.touchStartX = e.changedTouches[0].screenY
      console.log(11);
      
      console.log(e);
      
    },
    touchend (e) {
      e.preventDefault()
      console.log(e);
      
      console.log(e);
      
      if (this.touchStartX === 0) return

      const touchEndX = e.changedTouches[0].screenY

      if (this.scrollingLock) return

      if (this.touchStartX - touchEndX > 80) {
        this.scrollingLock = true

        setTimeout(() => {
          this.scrollingLock = false
        }, 700)

       if(this.currentPage === 4) return
         this.currentPage++
      } else if (this.touchStartX - touchEndX < -80) {
        this.scrollingLock = true

        setTimeout(() => {
          this.scrollingLock = false
        }, 700)

        if (this.currentPage === 0) return
        else this.currentPage--
      }

      this.touchStartX = 0
    }
  }
}
</script>
<style lang="less" src="@/assets/_base.less"></style>
<style lang="less" src="@/assets/common.less"></style>
<style lang="less" src="@/assets/entry.less"></style>
<style src="@/assets/common.less" >
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

</style>



