<template>
<aside>
            <div class="item">
                <a  @click="linkTo(0)"

                >
                        <div class="icon-waper homePage"
                                        :class="{'active-link': id === 0}"

                        >
                            <img src="@/static/svg/主页.svg" alt="">
                            
                        <p class="homePage-p">简介</p>
                        </div>
                        
                </a>
            </div>
            <div class="item">
                <a @click="linkTo(1)"

                >
                        <div class="icon-waper object"
                                      :class="{'active-link': id === 1}"

                        >
                            <img src="@/static/svg/项目.svg" alt="">
                        <p class="object-p">作品</p>
                        </div>
                        
                </a>
            </div>
            <div class="item">
                <a @click="linkTo(2)"

                >
                        <div class="icon-waper paper"
                                       :class="{'active-link': id === 2}"

                        >
                            <img src="@/static/svg/文章.svg" alt="">
                             <p class="paper-p">文章</p>
                        </div>
                </a>
            </div>
            <div class="item">
                <a @click="linkTo(3)"

                >
                        <div class="icon-waper skill"
                                                          :class="{'active-link': id === 3}"

                        >
                            <img src="@/static/svg/skill-9.svg" alt="">
                             <p class="skill-p">技能</p>
                        </div>
                        
                </a>
            </div>
            <div class="item">
                <a @click="linkTo(4)"
 
                  >
                        <div class="icon-waper me"
                                         :class="{'active-link': id === 4}"
                        >
                            <img src="@/static/svg/我.svg" alt="">
                          <p class="me-p">自我介绍</p>
                        </div>
                </a>
            </div>
        </aside>
</template>

<script>
import store from '@/store/store.js'
export default{
    name:'navbar',
      props: ['id'],
      methods: {
    linkTo (id) {
      this.$emit('linkTo', id)
    }
  }
}
</script>
   


<style lang="less" scoped>
* { margin: 0; padding: 0; } * { box-sizing: border-box; } *::after { box-sizing: border-box; } *::before { box-sizing: border-box; } ul, ol { list-style: none; } li.active{ outline: 1px solid; } a { color: inherit; text-decoration: none; } 
@keyframes bigger{
    0%{
        transform: scale(1)
    }
    100%{
        transform: scale(1.3)
    }
}

aside{
      position: absolute;
  top: 30%;
  right: 5%;
//   padding-right: 40px;
//   height: 100%;
//   width: 200px;
z-index: 2;
    .item{
        .active-link{
                            animation-name: bigger;
                animation-timing-function: ease-in-out; 
                animation-duration: 0.5s;
                animation-fill-mode : none;
                animation-fill-mode:forwards;

        }
        a{
            .icon-waper{
                position: relative;
                border-radius: 50%;
                padding: 5px;
                border-radius: 200px;
                background-image: -webkit-gradient(radial, center center, 0, center center, 212, from(#fcfcfc), to(#bfbfbf), color-stop(3%, #f7f7f7), color-stop(12%, #f2f2f2), color-stop(90%, #d9d9d9));
                background-image: -webkit-radial-gradient(center center, cover, #fcfcfc, #f7f7f7 3%, #f2f2f2 12%, #d9d9d9 90%, #bfbfbf);
                background-image: -moz-radial-gradient(center center, cover, #fcfcfc, #f7f7f7 3%, #f2f2f2 12%, #d9d9d9 90%, #bfbfbf);
                background-image: -o-radial-gradient(center center, cover, #fcfcfc, #f7f7f7 3%, #f2f2f2 12%, #d9d9d9 90%, #bfbfbf);
                background-image: -ms-radial-gradient(center center, cover, #fcfcfc, #f7f7f7 3%, #f2f2f2 12%, #d9d9d9 90%, #bfbfbf);
                background-image: radial-gradient(center center, cover, #fcfcfc, #f7f7f7 3%, #f2f2f2 12%, #d9d9d9 90%, #bfbfbf);
                -webkit-box-shadow: 0 1px 5px rgba(0,0,0,0.75), inset 0 0 27px rgba(189,189,189,0.7);
                -moz-box-shadow: 0 1px 5px rgba(0,0,0,0.75), inset 0 0 27px rgba(189,189,189,0.7);
                box-shadow: 0 1px 5px rgba(0,0,0,0.75), inset 0 0 27px rgba(189,189,189,0.7);
                background: #e5e5e5;
                margin: 10px;
                display: flex;
                justify-content: center;
                align-content: center;
                img{
                    width: 18px;
                    height: 18px;
                    background: rgb(216, 216, 216);
                }
            }
            p{
                display: none;
                border:1px solid red;
                position: relative;
            }
            .icon-waper:hover {
                animation-name: bigger;
                animation-timing-function: ease-in-out; 
                animation-duration: 0.5s;
                animation-fill-mode : none;
                animation-fill-mode:forwards;

            }

            .icon-waper:hover+p{
                position: absolute;
                left: -39px;
                top: 11px;

                border:1px solid red;
                display: inline-block;
                
            }
        }

    }
}
@media(max-width: 430px){
  aside{
    display: none;
  }
}
</style>



