<!--  -->
<template>
 <section class="page skill">

                <h1 class="title">技能</h1>
<div class="view">
	<div class="object">
		
			<div class="side front">
        <img src="@/static/img/jq.png" alt="">
			</div>
			<div class="side left">
        <img src="@/static/img/js.png" alt="">
			</div>
			<div class="side right">
        <img src="@/static/img/css.png" alt="">
			</div>
			<div class="side back">
        <img src="@/static/img/vue.png" alt="">
			</div>
			<div class="side top">
        <img src="@/static/img/js.png" alt="">
			</div>
			<div class="side bottom">
        <img src="@/static/img/html.png" alt="">
			</div>
		
	</div>
</div>
 </section>
</template>

<script>
export default {
  data () {
    return {
      items:[{front:require("@/static/img/html.png"),back:require("@/static/img/jq.png")},
      {front:require("@/static/img/js.png"),back:require("@/static/img/vue.png")},
      {front:require("@/static/img/css.png"),back:require("@/static/img/ps.png")}],
      action:false
    };
  },
  methods:{
    moveIn(item){
      this.$set(item,'action',true)
    },
    moveOut(item){
      this.$set(item,'action',false)
    }
  },


}

</script>
<style lang='stylus' src="./template.stylus" scoped>

</style>


