<script>
export default {
  data () {
    return {
       cover:false,
       music:false,
       blog:false,
       canvas:false,
          touchStartY: 0,
    currentPage: 0
    };
  },
  methods:{
          on(e){
          this.cover = true;
          if(e===1){
                  this.music=true
          }
          if(e===2){
                  this.blog=true
          }
        if(e===3){
                this.canvas=true
        }
          },
        off(){
         this.cover=false
         this.canvas=false
         this.music=false
         this.blog=false
        },
    touchmove (e) {
      if (this.touchStartX !== 0) return
      this.touchStartX = e.changedTouches[0].screenX
    },
    touchend (e) {
      if (this.touchStartX === 0) return

      const touchEndX = e.changedTouches[0].screenX

      if (this.touchStartX - touchEndX > 20) {
        if (this.currentPage === 2) return
        else this.currentPage++
      } else if (this.touchStartX - touchEndX < -20) {
        if (this.currentPage === 0) return
        else this.currentPage--
      }

      this.touchStartX = 0
    }
  }

}

</script>
